/* ==========================================================================
# Side area style - begin
========================================================================== */

.edgt-light-header .edgt-page-header > div:not(.edgt-sticky-header):not(.fixed) .edgt-side-menu-button-opener .edgt-side-menu-icon,
.edgt-light-header .edgt-top-bar .edgt-side-menu-button-opener .edgt-side-menu-icon{
	color: $header-light-color !important; //Color can be set inline from widget opener;
	
	&.opened,
	&:hover {
		color: $header-light-hover-color !important; //Color can be set inline from widget opener;
	}
}

.edgt-dark-header .edgt-page-header > div:not(.edgt-sticky-header):not(.fixed) .edgt-side-menu-button-opener .edgt-side-menu-icon,
.edgt-dark-header .edgt-top-bar .edgt-side-menu-button-opener .edgt-side-menu-icon{
	color: $header-dark-color !important; //Color can be set inline from widget opener;
	
	&.opened,
	&:hover {
		color: $header-dark-hover-color !important; //Color can be set inline from widget opener;
	}
}

.edgt-side-menu-button-opener {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	top: 2px;
	
	.edgt-side-menu-title {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		margin: 0 8px 0 0;
		color: currentColor;
	}
	
	.edgt-side-menu-icon {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		font-size: 14px;
		color: #212121;
		
		span {
			display: block;
			line-height: inherit;
			
			&:before {
				display: block;
				line-height: inherit;
			}
		}
	}
	
	&.opened,
	&:hover {
		color: $first-main-color;
	}
}

.edgt-side-menu {
    position: fixed;
    top: 0;
	right: -607px;
	width: 607px;
    height: 100%;
    min-height: 100%;
	padding: 72px 5.9% 36px;
    background-color: #fff;
	background-size: cover;
    text-align: center;
    overflow: hidden;
    visibility: hidden;
    z-index: 9999;
    -webkit-backface-visibility: hidden;
    box-sizing: border-box;
    @include edgtTransition(all .6s cubic-bezier(.77,0,.175,1));

    .edgt-close-side-menu-holder {
        display: none;
        position: absolute;
	    top: 25px;
	    right: 53px;
        z-index: 1000;
	
	    .admin-bar & {
		    top: 68px;
	    }
    }

	a.edgt-close-side-menu {
		display: block;
		color: #92919c;
		font-size: 30px;
		z-index: 1000;

		&:hover {
			color: #212121;
		}

		span {
			display: block;
			line-height: inherit;

			&:before {
				display: block;
				line-height: inherit;
			}
		}
	}

        .edgt-side-area-holders {
            display: table;
            table-layout: fixed;
            width: 100%;
            height: 100%;
        }

        .edgt-side-menu-top {
            display: table-row;
            width: 100%;
            height: 90%;
        }

        .edgt-side-menu-bottom {
            display: table-row;
            width: 100%;
            height: 10%;
        }

	.widget{
		&.widget_text {
			margin-bottom: 0;
		}
    }
}

.edgt-side-menu-slide-from-right {
	
	&.edgt-right-side-menu-opened {
		
		.edgt-wrapper {
			
			.edgt-cover {
				z-index: 3000;
			}
		}
		
		.edgt-side-menu {
			right: 0;
			visibility: visible;
            border-left: 1px solid #ebebeb;
		}
	}

    .edgt-wrapper {

        .edgt-cover {
            z-index: -1;
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            overflow: hidden;
            opacity: 0;
        }
    }
}
/* ==========================================================================
# Side area style - end
========================================================================== */